body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  
}

.btn {
  margin-top: 20px;
}
.fail {
  background-color: red;
}

.index {
  color: grey;
  display: none;
}